import { ArrowRightIcon, DownloadIcon } from "@heroicons/react/solid";
import React from "react";
import { FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faDownload } from "@fortawesome/free-solid-svg-icons";


export default function Footer() {
  return (
    <footer className="bg-blue-800 ">
      <div className="container mx-auto flex flex-wrap p-5 flex-col items-center">
        <a className="title-font font-medium  text-white mb-4 md:mb-0">
          <a href="https://github.com/GIrvine2" className="ml-3 text-xl text-white justify-center">
          <FontAwesomeIcon icon={faGithub} size="lg" />
          </a>
          <a href="https://www.linkedin.com/in/gavin-irvine-123381210" className="ml-3 text-xl text-white justify-center">
          <FontAwesomeIcon icon={faLinkedin} size="lg" />
          </a>
        </a>
      </div>
    </footer>
  );
}


