import React from "react";
import { ChevronDoubleDownIcon } from "@heroicons/react/solid";
import { BadgeCheckIcon, AnnotationIcon, MailIcon, PhoneIcon } from "@heroicons/react/solid";


export default function Contact() {
  return (
    <section id="contact" className="text-black bg-blue-400 body-font">
      <div className="container px-5 py-5 mx-auto text-center lg:px-40">
        <div className="flex flex-col w-full mb-20">
          <AnnotationIcon className="mx-auto inline-block w-10 mb-4 animate-bounce" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font mb-2 text-black">
            Contact Me
          </h1>
          <p className="mx-auto leading-relaxed text-base">
           Feel free to contact me using the information below!
          </p>
        </div>

      <div class="grid grid-cols-1 gap-4 ">
      <div class="max-w-md mx-auto bg-blue-500 rounded-xl shadow-md overflow-hidden md:max-w-2xl">
          <div class="md:flex">

        <div class="p-5">
        <h1 class="uppercase tracking-wide text-sm text-indigo-500 font-bold underline"></h1>
        <div class="grid grid-cols-1  space-y-2">
        <div className="bg-red-500 rounded flex p-2">
                <MailIcon className="text-black w-6 h-6 flex-shrink-0 mr-2" />
                <span className="title-font font-medium text-white">
                  gavinti@hotmail.co.uk
                </span>
        </div>
        <div className="bg-red-500 rounded flex p-2">
                <PhoneIcon className="text-black w-6 h-6 flex-shrink-0 mr-2" />
                <span className="title-font font-medium text-white">
                  07958205044
                </span>
        </div>
        </div>


        </div>
                
        </div>
        </div>

        <div class="max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden md:max-w-2xl">
          <div class="md:flex">


        
        </div>
        </div>

        
    </div>
    
    <br></br>


      </div>
    </section>
  );
}


